import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { OperationalLocation } from '@biz-nest/commerce-client';
import {
  AppNameService, CurrencyService, EventbusService, MainBarTitleService,
  openClose,
  ViewOneCompose
} from '@front-shared-lib';
import { Icrumb } from '@main-shared-lib';
import { AdminBackBarComponent } from '../../misc/admin-back-bar/admin-back-bar.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { FrontendLogger } from '@biz-nest/core-client';

@Component({
  selector: 'app-view-operational-location',
  standalone: true,
  imports: [
    RouterModule,
    DatePipe,
    AdminBackBarComponent,
    CurrencyPipe,
    AdminPageContentComponent
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-operational-location.component.html',
  styleUrl: './view-operational-location.component.scss'
})
export class ViewOperationalLocationComponent
implements OnInit, OnDestroy {
  viewOneCompose = new ViewOneCompose(
    OperationalLocation,
    { route: ['/operational-location/update'], eventName: 'updateOperationalLocation' },
    this.router,
    this.route,
    this.eventbus
  );

  title = 'View';
  breadCrumbs: Icrumb[] = [
    {
      name: 'Dashboard',
      icon: 'space_dashboard',
      link: '/'
    }, {
      name: 'Operational Location',
      icon: 'location_city',
      link: '/operational-location/list'
    }, {
      name: 'View Operational Location',
      icon: 'visibility'
    }
  ];

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    public currencyService: CurrencyService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  async ngOnInit() {
    try {
      await this.viewOneCompose.init();

      this.title = this.viewOneCompose.oneItem?.placeName || 'View';

      this.mainBarTitle.title = 'View Operational Location - ' + this.title;

      this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
      this.metaTagService.updateTag({
        name: 'description',
        content: `Easily manage and edit your company details to
          ensure accurate information and effective business operations.`
      });
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  ngOnDestroy() {
    this.viewOneCompose.cleanup?.();
  }
}

